
"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { TrendingUp, TrendingDown, PieChart } from "lucide-react"
import { Transaction, Category} from '../services/logic'
import Api from "../services/logic";

interface CategoryData {
  name: string
  value: number
  percentage: number
  color: string
}

interface CircularDiagramProps {
  transactions: Transaction[]
  categories: Category[]
}


const api = new Api();

// Predefined colors for the diagram
const CATEGORY_COLORS = [
  '#3B82F6', '#EF4444', '#10B981', '#F59E0B', '#8B5CF6',
  '#EC4899', '#06B6D4', '#84CC16', '#F97316', '#6366F1',
  '#14B8A6', '#F43F5E', '#8B5CF6', '#EC4899', '#06B6D4'
]

export default function CircularDiagram({ transactions, categories }: CircularDiagramProps) {
  const [diagramType, setDiagramType] = useState<"expense" | "income">("expense");
  
 
  
 
  // Process data for the circular diagram
  const getCategoryData = (): CategoryData[] => {
    const filteredTransactions = transactions.filter(t => 
      diagramType === "expense" ? (t.type === "expense" || t.type === "expence") : t.type === "income"
    )

    // Group by category and calculate totals
    const categoryMap = new Map<string, number>()
    
    filteredTransactions.forEach(transaction => {
      const categoryId = transaction.category
      const category = categories.find(c => c.id === categoryId)
      const categoryName = category?.name || "Uncategorized"
      const amount = Math.abs(transaction.amount)
      
      categoryMap.set(categoryName, (categoryMap.get(categoryName) || 0) + amount)
    })

    // Calculate total for percentages
    const total = Array.from(categoryMap.values()).reduce((sum, value) => sum + value, 0)
    
    // Convert to array and sort by value (descending)
    const categoryData = Array.from(categoryMap.entries())
      .map(([name, value], index) => ({
        name,
        value,
        percentage: total > 0 ? (value / total) * 100 : 0,
        color: CATEGORY_COLORS[index % CATEGORY_COLORS.length]
      }))
      .sort((a, b) => b.value - a.value)

    return categoryData
  }

  useEffect(() => {
    getCategoryData()
  }, [])

  const categoryData = getCategoryData()
  const totalAmount = categoryData.reduce((sum, item) => sum + item.value, 0)

  // Generate SVG path for each segment
  const generateSegmentPath = (startAngle: number, endAngle: number, radius: number): string => {
    const start = polarToCartesian(radius, radius, radius, endAngle)
    const end = polarToCartesian(radius, radius, radius, startAngle)
    const largeArcFlag = endAngle - startAngle <= 180 ? "0" : "1"
    
    return [
      "M", radius, radius,
      "L", start.x, start.y,
      "A", radius, radius, 0, largeArcFlag, 0, end.x, end.y,
      "Z"
    ].join(" ")
  }

  const polarToCartesian = (centerX: number, centerY: number, radius: number, angleInDegrees: number) => {
    const angleInRadians = (angleInDegrees - 90) * Math.PI / 180.0
    return {
      x: centerX + (radius * Math.cos(angleInRadians)),
      y: centerY + (radius * Math.sin(angleInRadians))
    }
  }

  // Generate segments for the diagram
  const generateSegments = () => {
    const radius = 100 // Increased radius for larger screens
    const segments = []
    let currentAngle = 0
    
    for (let i = 0; i < categoryData.length; i++) {
      const segment = categoryData[i]
      const segmentAngle = (segment.percentage / 100) * 360
      const endAngle = currentAngle + segmentAngle
      
      segments.push({
        ...segment,
        startAngle: currentAngle,
        endAngle: endAngle,
        path: generateSegmentPath(currentAngle, endAngle, radius)
      })
      
      currentAngle = endAngle
    }
    
    return segments
  }

  const segments = generateSegments()

  return (
    <Card className="w-full h-full max-w-none border-0 p-2 shadow-none">
      <CardHeader className="flex md:flex-row flex-col items-center justify-between px-5 gap-2">
        <CardTitle className="text-lg font-semibold flex items-center gap-2">
          {diagramType === "expense" ? "Expenses by Category" : "Income by Category"}
        </CardTitle>
        
        <Tabs value={diagramType} onValueChange={(value) => setDiagramType(value as "expense" | "income")}>
          <TabsList className="h-9">
            <TabsTrigger value="expense" className="text-sm h-8 px-4">
              <TrendingDown className="h-4 w-4 mr-1" />
              Expenses
            </TabsTrigger>
            <TabsTrigger value="income" className="text-sm h-8 px-4">
              <TrendingUp className="h-4 w-4 mr-1" />
              Income
            </TabsTrigger>
          </TabsList>
        </Tabs>
      </CardHeader>
      
      <CardContent className="p-2 md:p-4 mb-5 h-[calc(100%-80px)] overflow-auto ">
        {segments.length > 0 ? (
          <div className="flex flex-col xl:flex-row items-center justify-between gap-1 md:gap-2 h-full">
            {/* Circular Diagram - Responsive sizing */}
            <div className="relative shrink-0">
              {/* Mobile: 140px, Tablet: 160px, Desktop: 200px, Large: 220px */}
              <svg 
                width="120" 
                height="120" 
                className="transform -rotate-90 w-20 h-20 md:w-40 md:h-40 lg:w-48 lg:h-48 xl:w-52 xl:h-52 2xl:w-56 2xl:h-56"
              >
                {segments.map((segment, index) => (
                  <path
                    key={index}
                    d={segment.path}
                    fill={segment.color}
                    stroke="white"
                    strokeWidth="2"
                    className="transition-all duration-300 hover:opacity-80 cursor-pointer"
                  />
                ))}
              </svg>
              
              {/* Center text - Responsive sizing */}
              <div className="absolute -inset-5 flex items-center justify-center transform ">
                <div className="text-center">
                  <div className="text-xl font-bold md:text-2xl lg:text-3xl xl:text-4xl">
                    {totalAmount.toFixed(0)}
                    <div className="text-xs text-gray-500 md:text-sm lg:text-base">BYN</div>
                  </div>
                 
                </div>
              </div>
            </div>

            {/* Legend - Aligned to right with flex-end */}
            <div className="flex-col min-w-0 h-full flex justify-end">
              <div className="space-y-2 md:space-y-3 overflow-y-auto text-xs md:text-sm lg:text-base pr-2 max-h-32 md:max-h-40 lg:max-h-48 w-full max-w-md">
                {segments.map((segment, index) => (
                  <div key={index} className="flex items-center justify-between gap-1 md:gap-2 w-full">
                    <div className="flex items-center gap-2 md:gap-3 min-w-0 flex-1">
                      <div 
                        className="w-3 h-3 md:w-4 md:h-4 rounded-full shrink-0"
                        style={{ backgroundColor: segment.color }}
                      />
                      <span className="truncate font-medium">{segment.name}</span>
                    </div>
                    <div className="flex items-center gap-2 md:gap-3 shrink-0">
                      <span className="font-bold text-sm md:text-lg">{segment.percentage.toFixed(1)}%</span>
                      <span className="text-gray-500 text-xs md:text-sm">({segment.value.toFixed(1)})</span>
                    </div>
                  </div>
                ))}
              </div>
              <div className="mt-4 border-t text-sm md:text-base text-gray-600 text-center">
                <div className="flex flex-col sm:flex-row items-center justify-center gap-1 sm:gap-3">
                  <span>Total {diagramType}: <strong className="text-base md:text-lg">{totalAmount.toFixed(1)} BYN</strong></span>
                  <span className="hidden sm:inline">•</span>
                  <span><strong>{segments.length}</strong> categories</span>
                  <span className="hidden sm:inline">•</span>
                  <span>
                    <strong>{
                      transactions.filter(t => 
                        diagramType === "expense" ? (t.type === "expense" || t.type === "expence") : t.type === "income"
                      ).length
                      // t.type = diagramType).length
                    }</strong> transactions
                  </span>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className="flex items-center justify-center h-full text-gray-500 text-base md:text-lg">
            No {diagramType} data available
          </div>
        )}
      </CardContent>
    </Card>
  )
}