  



"use client";
import { useEffect, useState } from "react";
import AmountsTable from '../components/Amounts'
import CircularDiagram from '../components/Diagram'
import Api from "../services/logic"
import { Category, Transaction } from '../services/logic';
import {
    ResizableHandle,
    ResizablePanel,
    ResizablePanelGroup,
  } from "@/components/ui/resizable"

const api = new Api()

export default function ResizableDemo() {
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [categories, setCategories] = useState<Category[]>([])
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    async function fetchCategories() {
      try {
        const cats = await api.getAllCategories()
        if (Array.isArray(cats)) {
          setCategories(cats)
        } else {
          setCategories([])
        }
      } catch (error) {
        console.error('Error loading categories:', error)
        setCategories([])
      }
    }
    fetchCategories()
  }, [])

  useEffect(() => {
    async function fetchTransactions() {
      setLoading(true)
      try {
        const result = await api.getTransactions()
        setTransactions(result.data || [])
      } finally {
        setLoading(false)
      }
    }
    fetchTransactions()
  }, [])

  return (
    <div className="min-h-screen w-screen bg-background">
      <ResizablePanelGroup
        direction="horizontal"
        className="w-full h-full pt-20"
      >
        {/* Left Panel - Balance Summary */}
        <ResizablePanel 
          defaultSize={30} 
          minSize={20}
          maxSize={40}
          className="min-w-[300px]"
        >
          <div className="flex h-full w-full items-center justify-center p-4 bg-muted/50">
              <AmountsTable />
          </div>
        </ResizablePanel>
        
        <ResizableHandle 
          withHandle 
          className="bg-border hover:bg-primary/20 transition-colors"
        />
        
        {/* Right Panel - Analytics */}
        <ResizablePanel 
          defaultSize={70} 
          minSize={55}
          maxSize={75}
        >
          <ResizablePanelGroup direction="vertical">
            {/* Top Right Panel - Circular Diagram */}
            <ResizablePanel 
              defaultSize={70} 
              minSize={60}
              maxSize={80}
            >
              <div className="flex h-full w-full p-2 bg-card">
                <div className="w-full h-full">
                  <CircularDiagram 
                    transactions={transactions}
                    categories={categories}
                  />
                </div>
              </div>
            </ResizablePanel>
            
            <ResizableHandle 
              withHandle 
              className="bg-border hover:bg-primary/20 transition-colors"
            />
            
            {/* Bottom Right Panel - Transactions Summary */}
            <ResizablePanel 
              defaultSize={30} 
              minSize={15}
              maxSize={40}
            >
              <div className="flex h-full items-center justify-center p-4 bg-muted/30">
                <div className="text-center">
                  <span className="font-semibold text-lg">Transactions Summary</span>
                  <div className="mt-2 space-y-1 text-sm">
                    <p className="text-muted-foreground">
                      Total: <strong>{transactions.length}</strong> transactions
                    </p>
                    <p className="text-muted-foreground">
                      Income: <strong className="text-green-600">
                        {transactions.filter(t => t.type === "income").length}
                      </strong>
                    </p>
                    <p className="text-muted-foreground">
                      Expenses: <strong className="text-red-600">
                        {transactions.filter(t => t.type === "expense" || t.type === "expence").length}
                      </strong>
                    </p>
                  </div>
                </div>
              </div>
            </ResizablePanel>
          </ResizablePanelGroup>
        </ResizablePanel>
      </ResizablePanelGroup>
    </div>
  );
}